import React, { useRef, useState } from "react"

export default function App() {
    const firstNumber = useRef()
    const secondNumber = useRef()
    const [numberGenerated, setNumberGenerated] = useState()

    const randomRange = (min, max) => {
        return Math.floor(Math.random() * (max - min + 1)) + min
    }

    const generateANumber = () => {
        const f_number = firstNumber.current.value
        const s_number = secondNumber.current.value
        
        if (f_number === "") {
            alert("Te rog sa introduci primul numar")
        } else if (s_number === "") {
            alert("Te rog sa introduci al doilea numar")
        } else {
            const generate = randomRange(f_number, s_number)
            setNumberGenerated(generate)
        }
    }

    return (
        <>
            <input type="number" ref={firstNumber} placeholder="Introdu un nr" />   
            <input type="number" ref={secondNumber} placeholder="Introdu al doilea nr" />   
            <input type="button" onClick={generateANumber} value="Generate a random number" />
            <p>{numberGenerated}</p>
        </>
    )
}